$color-main: #0056A9;
$color-white: #ececec;


body {
    background: black; }

.space_background {
    min-height: 750px;
    background: url("../images/193.png"), $color-main 50%;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-blend-mode: screen;
    display: flex;
    flex-direction: column; }

.profile-data {
    margin: 0 auto;
    margin-top: 50px;
    margin-bottom: 25px;
    min-height: 50px;
    padding: 15px;
    width: 50vw;
    background-color: rgb(246, 246, 246);
    border-radius: 6px;
    box-shadow: 8px 5px 4px 0px rgba(0, 0, 0, 0.25);
    &__item {
        margin-bottom: 15px; }
    &__d {
        margin-bottom: 20px; }
    &__a {
        text-align: right;
        margin-bottom: 30px; }
    &__label {
        font-size: 14px;
        margin-bottom: 4px; }
    &__form-error {
        font-size: 14px;
        color: #d52e2e; }
    &__button {
        display: flex;
        flex-direction: column; } }

.form-control {
    display: block;
    width: 100%;
    height: calc(1.5em + 0.75rem + 2px);
    padding: 1.375rem 0.75rem;
    font-size: 1rem;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: 0.25rem; }

.readonly {
    width: 100%;
    height: calc(1em + 0.75rem + 2px);
    padding: 1.375rem 0.75rem;
    font-size: 1rem;
    color: #495057;
    background-color: #d9d9d9;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: 0.25rem; }

.modal {
    --bs-modal-width: 350px; }

.modal-content {
    background-color: #4a4a4a;
    &__body-text {
        color: $color-white;
        font-size: 22px;
        text-align: center; } }

@media (max-width: 767px) {
    .profile-data {
        &__label {
            font-size: 12px;
            margin-bottom: 4px; }
        &__item {
            margin-bottom: 7px; }
        &__a {
            font-size: 14px;
            margin-bottom: 15px; }
        input {
            font-size: 0.9rem; } }
    .readonly {
        padding: 1.1rem 0.75rem; }
    .form-control {
        padding: 1.1rem 0.75rem; }
    .errorlist {
        li {
            font-size: 12px; } }
    .btn {
        --bs-btn-font-size: 0.6rem;
        padding: 3px 6px; } }


@media (max-width: 587px) {
    .profile-data {
        &__label {
            font-size: 10px;
            margin-bottom: 4px; }
        &__item {
            margin-bottom: 7px; }
        &__a {
            font-size: 12px; }
        input {
            font-size: 0.8rem; } }
    .errorlist {
        li {
            font-size: 10px; } } }

@media (max-width: 460px) {
    .profile-data {
        width: 215px; } }
